.body {
  background: $color-bg;
  color: $color-text;
  font-family: $font-family;
  font-size: $font-size;
  letter-spacing: $letter-spacing;
  line-height: $line-height;
  scroll-behavior: smooth;

  &.--frozen {
      overflow: hidden;
  }
}

.container {
  max-width: $container-width;
  padding-left: $container-padding-left; 
  padding-right: $container-padding-right;
  margin: 0 auto;
  position: relative;
  z-index: 50;
  width: 100%;
}

.hidden {
  display: none;
}

.content{
  position: relative;
  z-index: 10;
}

.text {
  line-height: 26px;
}


a {
  cursor: pointer;
  color: $link-color;
  text-decoration: none;
}


strong {
  font-weight: bold;
}

::placeholder {
  color: $placeholder;
  transition: .4s;
}

:focus::placeholder {
  opacity: 0;
}

::-webkit-scrollbar {
  width: 0px; 
  background: transparent;
}

img{
  max-width: 100%;
  display: block;
}

form{
  display: block;
  width: 100%;
}



h1, h2, h3, h4, h5, h6{    
  font-weight: normal;
}

h1{
  font-weight: 600;
  font-size: 96px;
  line-height: 117px;
  font-family: $montserrat;
}

h3{
  font-family: $calibri;
  font-weight: bold;
  font-size: 68px;
  line-height: 59px;
}

h2{
  font-family: $calibri;
  font-weight: bold;
  font-size: 52px;
  line-height: 56px;
  margin-bottom: 45px;
  text-align: center;
}

.h2.--blue{
  color: #108DA8;
}

.h2.--white{
  color: #fff;
}


.inner{
  min-height: calc(100vh - 110px);
  background: url(../img/inner-1.svg) no-repeat top left / 1440px auto, url(../img/inner-2.svg) no-repeat right bottom / 663px auto;
  background-color: #7BCFBF;
}

@media(max-width: 768px){
  h2{
    margin-bottom: 36px;
  }
  
}


@media(max-width: 425px){

  .main__description{
    font-size: 32px;
    line-height: 39px;
    margin-bottom: 26px;
  }
  h2{
    font-size: 46px;
    margin-bottom: 26px;
  }
}