.jury{
  padding-bottom: 30px;
  padding-top: 35px;
  .form__wrap {
    min-height: calc(100vh - 225px);
}
}
.jury__list{
  display: flex;
  margin-right: -20px;
  flex-wrap: wrap;
  margin-bottom: 30px;
}

.jury__item {
  position: relative;
  overflow: hidden;
  margin-right: 20px;
  margin-bottom: 20px;
  border-radius: 10px;
  width: calc(25% - 20px);
}

.jury__photo{
  height: 337px;
}

.jury__info {
  background-color: #fff;
  height: 100%;
  width: 100%;
  position: absolute;
  transform: translateY(256px);
  transition: .4s;
  padding: 17px 20px;
  z-index: 100;
  top: 0;
  .jury__item:hover &{
    transform: translateY(0);
    background-color: rgba(#fff, .8);
  }
}

.jury__name{
  font-size: 18px;
  margin-bottom: 20px;
}

.jury__teams{
  max-width: 1014px;
  margin: 0 auto;
}

.jury-team{
  height: 98px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 46px;
  padding-right: 36px;
  margin-bottom: 16px;
  background-color: #fff;
  border-radius: 10px;
}

.jury-team__name{
  font-weight: bold;
  font-size: 32px;
  line-height: 105.57%;
  font-family: $calibri;
}

.jury-team__number{
  width: 68px;
  height: 68px;
  border-radius: 50%;
  background-color: #F15858;
  font-weight: bold;
  font-size: 40px;
  line-height: 105.57%;
  font-family: $calibri;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}


@media(max-width: 980px){
  .jury__item{
    width: calc(33.333% - 20px);
  }
}

@media(max-width: 768px){
  .jury .form__wrap{
    margin-bottom: 30px;
  }
  .jury__item{
    width: calc(50% - 20px);
  }
  .jury-team{
    padding-left: 20px;
    padding-right: 20px;
  }
  .jury-team__name{
    font-size: 24px;
  }
  .jury-team__number{
    width: 42px;
    height: 42px;
    font-size: 28px;
  }
}

@media(max-width: 550px){
  .jury__item{
    width: calc(100% - 20px);
  }
}