.news{
  padding-top: 46px;
  padding-bottom: 46px;
}
.news__list{
  display: flex;
  flex-wrap: wrap;
  margin-right: -20px;
}

.news__panel{
  border-radius: 20px;
  overflow: hidden;
  position: relative;
  margin-bottom: 36px;
  margin-right: 20px;
  &--50{
    width: calc(50% - 20px);
  }
  &--33{
    width: calc(33.33% - 20px);
  }
}

.news__pic{
  height: 400px;
  width: 100%;
}

.news__info{
  min-height: 140px;
  padding: 18px 36px;
  background-color: rgba(#fff, .8);
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
}

.news__title{
  font-weight: bold;
  font-size: 28px;
  line-height: 34px;
  font-family: $calibri;
  height: 63px;
  margin-bottom: 10px;
}

.news__date{
  font-size: 20px;
  line-height: 23px;
}


.news-inner{
  background: url(../img/news-inner-top.svg) no-repeat top -154px left / 1950px auto, url(../img/news-inner-bottom.svg) no-repeat bottom right / 1950px auto;
  background-color: #fff;
  padding-top: 170px;
}

.news-inner-wrap{
  max-width: 1014px;
  margin: 0 auto;
}

.crumbs{
  display: flex;
  margin-bottom: 36px;
  flex-wrap: wrap;
}

.crumbs__link{
  span{
    color: #53B3A1;
    padding-right: 5px;
    padding-left: 3px;
  }
  &:last-child{
    color: #BDBDBD;
    span{
      display: none;
    }
  }
}

.inner-news__title{
  font-weight: bold;
  font-size: 56px;
  line-height: 87.07%;
  font-family: $calibri;
  margin-bottom: 26px;
}

.inner-news__date{
  font-size: 18px;
  line-height: 21px;
  margin-bottom: 36px;
}

.inner-news__pic{
  border-radius: 20px;
  overflow: hidden;
  margin-bottom: 56px;
  img{
    width: 100%;
  }
}

.inner-news__small-title{
  font-weight: bold;
  font-size: 32px;
  line-height: 105.57%;
  font-family: $calibri;
  margin-bottom: 46px;
}

.inner-news__small-text {
  margin-bottom: 46px;
  p{
    font-size: 18px;
    line-height: 21px;
    margin-bottom: 22px;
    &:last-child{
      margin-bottom: 0px;
    }
  }
}

.inner-news__subscribe{
  p{
    font-size: 20px;
    margin-bottom: 26px;
  }
}

.inner-news__socials{
  display: flex;
  margin-bottom: 54px;
}

.inner-news__social{
  width: 144px;
  border-radius: 6px;
  margin-right: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 46px;
  background-color: #68c0af;
  &:last-child{
    margin-right: 0px;
  }
}

.other__news {
  .inner__small-title{
    background-color: transparent;
    margin-bottom: 46px;
    padding: 0;
  }
  .form__socials{
    padding-bottom: 30px;
  }
  .news__pic{
    height: 373px;
  }
}

@media(max-width: 980px){
  .news__panel--33{
    width: calc(50% - 20px);
  }
}

@media(max-width: 768px){
  .crumbs__link{
    font-size: 14px;
  }
  .inner-news__title{
    background-color: #fff;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 5px;
    margin-bottom: 0px;
    font-size: 38px;
  }
  .inner-news__date{
    padding: 5px;
    background-color: #fff;
  }
  .inner-news__pic{
    margin-bottom: 30px;
  }
  .inner-news__small-title{
    font-size: 25px;
    margin-bottom: 22px;
  }
  .inner-news__small-text {
    p{
      font-size: 16px;
    }
  }
  .inner-news__subscribe p{
    font-size: 18px;
  }
  .inner-news__social{
    width: 70px;
  }
}

@media(max-width: 650px){
  .news__panel--33{
    width: calc(100% - 20px);
  }
  .news__panel--50{
    width: calc(100% - 20px);
  }
  .news__info{
    padding-left: 20px;
    padding-right: 20px;
    height: auto;
    min-height: auto;
  }
  .news__title{
    font-size: 20px;
    line-height: 24px;
    min-height: 45px;
    height: auto;
  }
  .news__date{
    font-size: 16px;
    line-height: 18px;
  }
}