.partic{
  &.--white{
    background-color: #fff;
  }
  .input input{
    border: none;
  }
  &.active{
    .others-winners{
      display: none;
    }
  }
}

.partic{
  background: url(../img/inner-1.svg) no-repeat top left / 1440px auto, url(../img/inner-2.svg) no-repeat right bottom / 663px auto, url(../img/partic-top.svg) no-repeat  left 0 top -400px / 1950px auto;
}

.form__socials-partic{
  padding-bottom: 30px;
}

@media(min-width: 1950px){
  .partic{
    background: url(../img/inner-1.svg) no-repeat top left / 1440px auto, url(../img/inner-2.svg) no-repeat right bottom / 663px auto, url(../img/partic-top.svg) no-repeat  0 top / 100% auto;
  }
}

.partic__top{
  color: #fff;
  padding-top: 36px;
  padding-bottom: 207px;
  margin-top: -22px;
  .select{
    color: $color-base;
  }
}



.inner__title{
  font-weight: bold;
  font-size: 62px;
  line-height: 87.07%;
  margin-bottom: 50px;
  color: #fff;
  font-family: $calibri;
}

.form__block--search{
  .input {
    flex-grow: 1;
  }
}

.switch__block{
  width: 311px;
  display: flex;
  justify-content: center;
  align-items: center;
  .switch{
    margin-right: 20px;
  }
}

.switch{
 width: 78px;
 height: 39px;
 border-radius: 100px;
 background-color: #BDBDBD;
 position: relative;
 cursor: pointer;
 &.active{
   background-color: #53B3A1;
   &:after{
     border-color: #53B3A1;
     left: 39px;
   }
 }
 &:after{
   content: "";
   width: 39px;
   height: 39px;
   border-radius: 50%;
   position: absolute;
   left: 0;
   top: 0;
   background-color: #fff;
   border: 2px solid #BDBDBD;
   transition: .4s;
 } 
}

.selects{
  margin-bottom: 16px;
}

.selects__block{
  width: 100%;
  max-width: 248px;
  margin-right: 30px;
  &:last-child{
    margin-right: 0px;
  }
}

.inner__small-title{
  padding: 10px;
  display: inline-block;
  background-color: #7BCFBF;
  font-weight: bold;
  font-size: 46px;
  line-height: 107.07%;
  font-family: $calibri;
  margin-bottom: 26px;
}


.winner {
  display: flex;
  justify-content: space-between;
  background-color: #fff;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
  margin-bottom: 26px;
  &:last-child{
    margin-bottom: 0px;
  }
}


.winner__pic{
  width: 290px;
  min-height: 242px;
}

.winner__info {
  width: calc(100% - 290px);
  padding: 36px 46px;
  color: #000;
}

.winner__title{
  font-family: Calibri;
  font-weight: bold;
  font-size: 38px;
  line-height: 46px;
  margin-bottom: 8px;
}

.winner__description{
  font-size: 22px;
  line-height: 26px;
  margin-bottom: 57px;
}

.winner__tags{
  display: flex;
}

.winner__tag{
  display: flex;
  height: 33px;
  margin-right: 12px;
  font-size: 18px;
  justify-content: center;
  align-items: center;
  white-space: nowrap;
  &:last-child{
    margin-right: 0px;
  }
  &.--green{
    width: 72px;
    min-width: 72px;
    color: #41BD22;
    background: url(../img/gree-tag.svg) no-repeat right top / contain;
  }
  &.--orange{
    border: 1px solid #F2724A;
    border-radius: 4px;
    color: #F2724A;
    padding-left: 10px;
    padding-right: 10px;
  }
  &.--blue{
    border: 1px solid #72B0FA;
    border-radius: 4px;
    color: #72B0FA;
    padding-left: 10px;
    padding-right: 10px;
  }
}

.prize{
  position: absolute;
  right: 24px;
  top: 24px;
  border-radius: 50%;
  width: 77px;
  height: 77px;
  overflow: hidden;
  z-index: 100;
  .--gold &{
    background: #fff url(../img/gold.svg) no-repeat center / auto;
  }
  .--silver &{
    background: #fff url(../img/silver.svg) no-repeat center / auto;
  }
}


.others-winners  {
  .winner__tags{
    flex-wrap: wrap;
  }
  .winner__tag{
    margin-bottom: 10px;
  }
  .inner__small-title{
    width: 100%;
    background-color: transparent;
    padding: 0;
    margin-bottom: 46px;
  }
  .winner{
    width: calc(33.33% - 21px);
    margin-right: 21px;
    border: 1px solid #53B3A1;
    margin-bottom: 36px;
  }

  .winner__info{
    width: 100%;
  }
  .form__socials{
    padding-bottom: 30px;
  }
}

.others-winners__list{
  margin-right: -21px;
}


@media(max-width: 980px){
  .partic__top{
    margin-top: -20px;
    background-position: left top;
  }
  .winner__tags{
    flex-wrap: wrap;
  }
  .winner__tag{
    margin-bottom: 10px;
  }

  .winner__title{
    padding-right: 45px;
  }
  .others-winners .winner{
    width: calc(50% - 21px);
  }
}


@media(max-width: 768px){
  .inner__title{
    font-size: 56px;
    line-height: 87.07%;
  }
  .form__block--search{
    display: block;
  }
  .switch__block{
    justify-content: flex-start;
    width: 100%;
    margin-bottom: 10px;
    br{
      display: none;
    }
  }
  .selects__block{
    width: 100%;
    max-width: 100%;
    margin-right: 0px;
  }
  .others-winners .winner{
    width: calc(100% - 21px);
  }
  .winner__title{
    font-size: 28px;
    margin-bottom: 12px;
    line-height: 34px;
  }
  .winner__description{
    font-size: 18px;
    line-height: 21px;
    margin-bottom: 20px;
  }
}

@media(max-width: 640px){
  .winner{
    display: block;
  }
  .winner__pic{
    width: 100%;
    height: 176px;
  }
  .partic{
    background: url(../img/inner-1.svg) no-repeat top left / 680px auto, url(../img/inner-2.svg) no-repeat right bottom / 363px auto, url(../img/partic-top-mobile.svg) no-repeat  left 0 top 0 / 2050px auto;
  }
  .winner__info{
    width: 100%;
    padding: 16px 26px;
  }
  .partic__top{
    padding-bottom: 90px;
  }
}