.main{
  padding-top: 190px;
  padding-bottom: 276px;
  color: #fff;
  background: url(../img/main.svg) no-repeat center bottom / 1950px auto;
}

@media(min-width: 1950px){
  .main{
    background: url(../img/main.svg) no-repeat center bottom / 100% auto;
  }
}

.main__description{
  font-family: Calibri;
  font-size: 48px;
  line-height: 59px;
  margin-bottom: 26px;
}

.main__title{
  margin-bottom: 26px;
}

.main__text{
  max-width: 740px;
  margin-bottom: 52px;
}

.main__btns{
  .btn.--red{
    margin-right: 26px;
  }
}



.tasks {
  margin-top: 20px;
  margin-bottom: 38px;
}

.tasks__list{
  max-width: 1020px;
  margin: 0 auto -46px;
  justify-content: space-between;
}

.task{
  width: calc(50% - 10px);
  margin-bottom: 46px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.task__pic{
  width: 120px;
  height: 120px;
  border-radius: 50%;
  overflow: hidden;
}

.task__text{
  width: calc(100% - 152px);
}

.timer {
  background: url(../img/timer.svg) no-repeat center / 1950px auto;
  padding: 130px 0 110px;
  .btn.--red{
    margin: 0 auto;
  }
}

@media(min-width: 1950px){
  .timer{
    background: url(../img/timer.svg) no-repeat center bottom / 100% auto;
  }
}

.timer__block{
  display: flex;
  justify-content: center;
  max-width: 674px;
  box-shadow: 2px 4px 30px #79CDBD;
  border-radius: 20px;
  background-color: #fff;
  padding: 23px 0 26px;
  margin: 0 auto 44px;
  width: 100%;
  
}


.timer__item--center{
  padding-left: 102px;
  padding-right: 102px;
  position: relative;
  .timer__number{
    &:after,
    &::before{
      content: ":";
      position: absolute;
      top: 10px;
      font-weight: 600;
      font-size: 72px;
      line-height: 88px;
    }
    &:after{
      left: 100%;
      margin-left: 32px;
    }

    &:before{
      right: 100%;
      margin-right: 32px;
    }
  }
}

.timer__item{
  text-align: center;
}

.timer__number{
  color: #108DA8;
  font-weight: 600;
  font-size: 96px;
  line-height: 117px;
  font-family: $montserrat;
  position: relative;
}

.timer__text{
  font-size: 24px;
  line-height: 28px;
}


.calendar{
  padding-top: 15px;
}

.calendar__list{
  display: flex;
  justify-content: space-between;
}

.calendar__item{
  width: calc(33.333% - 10px);
}

.calendar__pic{
  margin-bottom: 35px;
  display: flex;
  justify-content: center;
}

.calendar__name{
  font-size: 30px;
  line-height: 35px;
  color: #108DA8;
  margin-bottom: 10px;
  text-align: center;
}

.calendar__text {
  font-size: 24px;
  line-height: 28px;
  text-align: center;
}


.organization{
  background: url(../img/organization.svg) no-repeat center top / 1950px auto;
  padding-top: 133px;
  padding-bottom: 111px;
  h2{
    margin-bottom: 70px;
  }
}

@media(min-width: 1950px){
  .organization{
    background: url(../img/organization.svg) no-repeat center top / 100% auto;
  }
}


.organization__list{
  display: flex;
  justify-content: center;
  margin-bottom: 83px;
}

.organization__item{
  margin-left: 30px;
  margin-right: 30px;
  width: 100%;
  max-width: 480px;
  box-shadow: 2px 4px 30px #73CFB7;
  border-radius: 20px;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 213px;
  padding-left: 40px;
  padding-right: 40px;
}

.organization__text{
  width: calc(100% - 160px);
  font-size: 20px;
}

.footer__logo{
  margin-right: 56px;
}

.socials__link{
  margin-right: 16px;
  &:last-child{
    margin-right: 0px;
  }
}

.footer__socials{
  margin-right: 36px;
}


@media(max-width: 1200px){
  .organization{
    background: url(../img/organization-mobile.svg) no-repeat -655px top / 2030px auto;
  }
  .footer{
    .btn.--enter{
      display: none;
    }
  }
}


@media(max-width: 980px){
  .organization {
    background: url(../img/organization-mobile.svg) no-repeat -974px top/auto 150%;
    padding-bottom: 50px;
}
  .tasks__list{
    margin-bottom: -35px;
  }
  .task{
    width: 100%;
    margin-bottom: 35px;
  }
  .task__pic{
    width: 100px;
    height: 100px;
  }
  .task__text{
    width: calc(100% - 133px);
  }
  .calendar__list{
    display: block;
  }
  .calendar__item{
    margin: 0 auto 30px;
    width: 100%;

  }
  .calendar__pic{
    max-width: 320px;
    margin: 0 auto 34px;
  }
  .organization__list{
    display: block;
    white-space: nowrap;
    overflow-y: auto;
    width: calc(100% + 40px);
    margin-left: -20px;
    padding-left: 20px;
    text-align: center;
  }
  .organization__item{
    width: 333px;
    margin-right: 24px;
    display: inline-block;
    vertical-align: top;
    height: 191px;
    padding: 0 28px;
    font-size: 0px;
    padding-top: 54px;
    margin-left: 0px;
  }
  .organization__pic{
    width: 90px;
    display: inline-block;
    vertical-align: top;
    margin-right: 24px;
  }
  .organization__text{
    width: calc(100% - 114px);
    font-size: 18px;
    display: inline-block;
    vertical-align: top;
    white-space:normal;
    text-align: left;
  }
  .footer{
    display: block;
  }
  .footer__left {
    display: block;
  }
  .footer__logo{
    width: 118px;
    margin: 0 auto 53px;
  }

  .footer__nav{
    flex-direction: column;
    max-width: 160px;
    text-align: center;
    margin: 0 auto;
  }

  .nav__link{
    margin-right: 0px;
    margin-bottom: 40px;
  }
  .footer__socials{
    justify-content: center;
    width: 100%;
    margin-right: 0px;
  }
  .socials__link{
    width: 44px;
    height: 44px;
  }
}


@media(max-width: 768px){
  .timer{
    background-size: auto 100%;;
    background-position: right -200px center;
  }
  .timer__number{
    font-weight: 600;
    font-size: 72px;
    line-height: 88px;
  }
  .timer__text{
    font-size: 20px;
    line-height: 23px;
  }
  .timer__item--center{
    padding-left: 50px;
    padding-right: 50px;
  }
  .timer__item--center .timer__number:after, .timer__item--center .timer__number::before{
    font-size: 46px;
    line-height: 56px;
  }
  .timer__item--center .timer__number:after{
    margin-left: 20px;
  }

  .timer__item--center .timer__number:before{
    margin-right: 20px;
  }
  .main{
    background-image: url(../img/main-mobile.svg);
    background-position: right 0 top;
    background-size: auto 100%;
    padding-top: 18px;
    padding-bottom: 140px;
  }

  .main__description{
    font-size: 38px;
    margin-bottom: 26px;
  }

  .main__title{
    font-size: 62px;
    padding-bottom: 26px;
    background-color: #7BCFBF;
    margin-bottom: 0px;
  }

  .main__text{
    font-size: 18px;
    background-color: #7BCFBF;
  }

  .main__btns{
    display: block;
    .btn{
      width: 100%;
    }
    .btn.--red{
      margin-bottom: 24px;
    }
  }
  .timer .btn.--red{
    width: 100%;
  }
}



@media(max-width: 425px){
  .calendar__pic{
    max-width: 280px;
  }
  .calendar__name{
    font-size: 26px;
    margin-bottom: 10px;
  }
  .calendar__text{
    font-size: 22px;
  }
 
  .task{
    display: block;
  }
  .task__pic{
    margin: 0 auto 20px;
  }
  .task__text{
    width: 100%;
  }

  .timer__number{
    font-size: 50px;
    line-height: 61px;
    margin-bottom: 0px;
  }
  .timer__text{
    font-size: 16px;
    line-height: 19px
  }
  .timer__item--center{
    padding-left: 46px;
    padding-right: 46px;
  }
  .timer__item--center .timer__number:after, .timer__item--center .timer__number::before{
    font-size: 46px;
    line-height: 56px;
    top: 0px;
  }
  .timer__item--center .timer__number:after{
    margin-left: 20px;
  }

  .timer__item--center .timer__number:before{
    margin-right: 20px;
  }
  .organization__item{
    height: 200px;
    width: 257px;
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 28px;
  }
  .organization__pic{
    display: block;
    margin-right: 0px;
    margin: 0 auto 15px;
  }

  .organization__text{
    width: 100%;
    font-size: 16px;
    text-align: center;
  }
  .main__description{
    font-size: 32px;
    line-height: 39px;
    margin-bottom: 26px;
    padding-left: 20px;
    margin-left: -20px;
    background-color: #7BCFBF;
  }
  .main__title{
    font-size: 56px;
    line-height: 87.07%;
    padding-left: 20px;
    margin-left: -20px;
  }
  .main__text{
    padding-left: 20px;
    margin-left: -20px;
    padding-bottom: 10px;
    margin-bottom: 30px;
  }
}