.btn{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 68px;
  border-radius: 6px;
  transition: .4s;
  text-transform: uppercase;
  &.--enter{
    background-color: #DDFF54;
    padding-left: 24px;
    padding-right: 24px;
    &:hover{
      background-color: #01A700;
      color: #fff;
    }
  }
  &.--red{
    background-color: #F15858;
    min-width: 237px;
    color: #fff;
    padding-left: 26px;
    padding-right: 26px;
    font-size: 18px;
    &:hover{
      background-color: #922D69;
    }
  }
  &.--blue{
    min-width: 210px;
    padding-left: 22px;
    padding-right: 22px;
    background-color: #10ACC5;
    color: #fff;
    font-size: 18px;
    &:hover{
      background-color: #08716D;
    }
  }
  &.--submit{
    width: 100%;
    color: #fff;
    background-color: #68C0AF;
    margin-top: 34px;
    &.--disabled{
      background-color: #E0E0E0;
    }
  }
}

@media(max-width: 460px){
  .btn{
    height: 56px;
  }
}