.form{
  padding-bottom: 60px;
}

.form__top{
  min-height: calc(100% - 108px);
  display: flex;
  justify-content: center;
  width: 100%;
  align-items: center;
  padding-top: 27px;
  padding-bottom: 27px;
}

.form__socials{
  width: 100%;
  align-self: flex-end;
}

.form__wrap{
  min-height: calc(100vh - 110px);
}

.form__panel{
  background-color: #fff;
  box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.08);
  border-radius: 16px;
  padding: 46px 78px;
  width: 100%;
  max-width: 550px;
}

.form__title{
  font-size: 38px;
  line-height: 46px;
  margin-bottom: 27px;
  text-align: center;
  font-family: $calibri;
}

.form__description{
  text-align: center;
  margin-bottom: 34px;
  margin-top: -10px;
}

.input__name{
  margin-bottom: 12px;
}

.input{
  position: relative;
  margin-bottom: 20px;
  &.--error input{
    background-color: #F15858;
    color: #fff;
    border-color: #F15858;
  }
  &.--succed input{
    background-color: #7BCFBF;
    color: #fff;
    border-color:#7BCFBF ;
  }
  input{
    font-size: 20px;
    padding-left: 20px;
    padding-right: 40px;
    width: 100%;
    height: 64px;
    border: 1px solid #828282;
    border-radius: 6px;
    background-color: #fff;
    &:focus{
      border: 2px solid #68C0AF;
    }
  }
}

.form__links{
  margin-bottom: 33px;
}

.form__link{
  color: #353535;
}

.error__message{
  margin-top: 10px;
  color: #F15858;
  display: none;
  .--error  &{
    display: block;
  }
}

.succed{
  position: absolute;
  right: 21px;
  top: 54px;
  display: none;
  .--succed  &{
    display: block;
  }
}

.error{
  position: absolute;
  right: 21px;
  top: 50%;
  transform: translateY(-50%);
  display: none;
  .--error  &{
    display: block;
  }
}


.form__sended {
  display: flex;
  justify-content: center;
  margin-bottom: 34px;
  padding-top: 18px;
}

.select__main{
  position: relative;
  input{
    cursor: pointer;
  }
  &.active{
    &:after{
      transform: translateY(-50%) rotateZ(180deg);
    }
  }
  &:after{
    content: "";
    position: absolute;
    right: 21px;
    top: 50%;
    transform: translateY(-50%) rotateZ(0);
    width: 17px;
    height: 10px;
    background: url(../img/select.svg) no-repeat center / contain;
    transition: .4s;
  }
}

.select__drop{
  position: absolute;
  width: 100%;
  left: 0;
  top: 100%;
  margin-top: 10px;
  box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.08);
  border-radius: 6px;
  background-color: #fff;
  z-index: 200;
  display: none;
}

.select__item{
  padding: 5px 10px;
  &:hover{
    background-color: #68C0AF;
    color: #fff;
  }
}


@media(max-width: 768px){
  .form__panel{
    padding: 26px 20px;
  }
  .input{
    margin-bottom: 20px;
  }
  .input input{
    height: 55px;
  }
  .succed{
    top: 48px;
  }
  .form__title{
    font-size: 32px;
    line-height: 39px;
  }
  .form__sended{
    margin-bottom: 24px;
    .svgsprite._sended{
      width: 54px;
      height: 54px;
    }
  }
}

@media(max-width: 500px){
  .form__link{
    width: 100%;
    margin-bottom: 14px;
    text-align: center;
    &:last-child{
      margin-bottom: 0px;
    }
  }
  .btn.--submit{
    margin-top: 22px;
  }
}