.team.--white{
  background: none;
  background-color: #fff;
  .inner__small-title{
    background-color: transparent;
    margin-bottom: 26px;
  }
}

.team__head{
  background: url(../img/team-top.svg) no-repeat bottom center / 1950px auto;
  padding-top: 80px;
  padding-bottom: 250px;
}

@media(min-width: 1950px){
  .team__head{
    background: url(../img/team-top.svg) no-repeat bottom center / 100% auto;
  } 
}

.team__pic{
  width: 500px;
  border-radius: 20px;
}

.team__info{
  width: calc(100% - 560px);
}

.team__tags{
  display: flex;
  margin-bottom: 34px;
}

.team__tag{
  padding-left: 10px;
  padding-right: 10px;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
  border: 1px solid #fff;
  font-size: 20px;
  line-height: 23px;
  color: #fff;
  margin-right: 11px;
  &:last-child{
    margin-right: 0px;
  }
  &.--white-arrow{
    width: 76px;
    border: none;
    padding-left: 0;
    padding-right: 0;
    background: url(../img/tag-white.svg) no-repeat center / 100% auto;
  }
}




.team__description{
  font-size: 22px;
  line-height: 26px;
  color: #fff;
  margin-bottom: 24px;
}

.team__region {
  font-size: 20px;
  line-height: 23px;
  color: #fff;
  b{
  font-weight: 500;
  padding-right: 5px;
  }
}

.team__project{
  margin-bottom: 44px;
}

.team__project-left{
  width: calc(100% - 445px);
}

.team__project-right {
  width: 393px;
}

.team__project-title {
  font-weight: bold;
  font-size: 32px;
  line-height: 105.57%;
  font-family: $calibri;
  margin-bottom: 36px;
}

.team__project-text {
  margin-bottom: 36px;
  height: 63px;
  overflow: hidden;
  &.active{
    height: auto;
  }
  b{
    font-weight: 500;
  }
  font-size: 18px;
  line-height: 21px;
}

.team__all-text{
  font-size: 18px;
  line-height: 21px;
  width: 100%;
  max-width: 248px;
  height: 56px;
  border-radius: 6px;
  color: #fff;
  text-transform: uppercase;
  background-color: #68C0AF;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: .3s;
  &:hover{
    background-color: #08716D;
  }
}


.team__partic{
  background: #FAF0E7;
  border-radius: 10px;
  padding: 26px;
}

.team__partic-user{
  display: flex;
  margin-bottom: 10px;
  align-items: center;
  justify-content: space-between;
  &:last-child{
    margin-bottom: 0px;
  }
}

.team__partic-avatar{
  width: 46px;
  height: 46px;
  border-radius: 50%;
  overflow: hidden;
}

.team__partic-name{
  font-size: 18px;
  line-height: 21px;
  width: calc(100% - 64px);
}

.team__partic-title{
  font-weight: bold;
  font-size: 26px;
  line-height: 32px;
  margin-bottom: 8px;
}

.gallery{
  margin-bottom: 50px;
}

.gallery__slider{
  overflow: hidden;
  position: relative;
}

.gallery__pic{
  margin-bottom: 20px;
}

.gallery__description{
  font-size: 22px;
  line-height: 26px;
}

.next,
.prev{
  position: absolute;
  top: 50%;
  width: 64px;
  height: 64px;
  border: 2px solid #7BCFBF;
  border-radius: 10px;
  z-index: 200;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.next{
  right: 0;
  transform: translateY(-50%) translateX(50%);
}

.prev{
  left: 0;
  transform: translateY(-50%) translateX(-50%);
}

.gallery__slider-wrap{
  .next{
    right: 0;
    margin-top: -2%;
  }
  
  .prev{
    left: 0;
    margin-top: -2%;
  }
}


.video{
  background: url(../img/video__bg.svg) no-repeat center / auto 100%;
  padding-top: 46px;
  padding-bottom: 160px;
  min-height: 630px;
  .inner__small-title{
    color: #fff;
    text-align: center;
    width: 100%;
    margin-bottom: 61px;
  }
}

@media(min-width: 1950px){
  .video{
    background: url(../img/video__bg.svg) no-repeat center / 100% auto;
  }
}

.video__slider{
  overflow: hidden;
}
.video__slide{
  padding: 16px;
  border-radius: 20px;
  background-color: #fff;
}
.video__frame {
  border-radius: 16px;
  overflow: hidden;
  margin-bottom: 18px;
}
.video__frame iframe{
  width: 100%;
  height: 211px;
}

.video__link{
  align-self: flex-start;
}

.video__info{
  display: flex;
  justify-content: space-between;
}

.team__docs-list{
  display: flex;
  flex-wrap: wrap;
  margin-right: -20px;
}
.team__docs{
  margin-bottom: 60px;
}
.team__doc{
  width: calc(50% - 20px);
  margin-right: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 40px;
  background: #F0F0F0;
  border-radius: 10px;
  margin-bottom: 20px;
}

.team__doc-ico{
  width: 64px;
  height: 64px;
  border-radius: 50%;
  background-color: #68C0AF;
  display: flex;
  align-items: center;
  justify-content: center;
}

.team__doc-text{
  font-size: 18px;
  line-height: 21px;
  width: calc(100% - 98px);
}

.score__table{
  border-radius: 20px;
  overflow: hidden;
}

.score__head{
  display: flex;
}

.score__text{
  min-height: 73px;
  display: flex;
  align-items: center;
  padding-left: 24px;
  background-color: #108DA8;
  padding-top: 26px;
  padding-bottom: 26px;
  color: #fff;
  width: calc(100% - 800px);
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  border-left: 1px solid #F0F0F0;
}

.score__tr{
  display: flex;
  background-color: #fff;
  &:nth-child(2){
    background-color: #F8F8F8;
  }
}

.score__point{
  width: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-left: 1px solid #F0F0F0;
  &:nth-child(2) span{
    background-color: #C92E2E;
  }
  &:nth-child(3) span{
    background-color: #E13C3C;
  }
  &:nth-child(4) span{
    background-color: #F15858;
  }
  &:nth-child(5) span{
    background-color: #E4CF12;
  }
  &:nth-child(6) span{
    background-color: #EDD927;
  }
  &:nth-child(7) span{
    background-color: #F9E84D;
  }
  &:nth-child(8) span{
    background-color: #53B3A1;
  }
  &:nth-child(9) span{
    background-color: #68C0AF;
  }
  &:nth-child(10) span{
    background-color: #7BCFBF;
  }
  &:nth-child(11) span{
    background-color: #10ACC5;
  }
  input{
    display: none;
  }
  span{
    width: 19px;
    height: 19px;
    display: block;
    border-radius: 50%;
    opacity: 0;
  }
  input:checked + span{
    opacity: 1;
  }
}

.score__number{
  width: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  color: #fff;
  &:nth-child(2){
    background-color: #C92E2E;
  }
  &:nth-child(3){
    background-color: #E13C3C;
  }
  &:nth-child(4){
    background-color: #F15858;
  }
  &:nth-child(5){
    background-color: #E4CF12;
  }
  &:nth-child(6){
    background-color: #EDD927;
  }
  &:nth-child(7){
    background-color: #F9E84D;
  }
  &:nth-child(8){
    background-color: #53B3A1;
  }
  &:nth-child(9){
    background-color: #68C0AF;
  }
  &:nth-child(10){
    background-color: #7BCFBF;
  }
  &:nth-child(11){
    background-color: #10ACC5;
  }
}


.team.--white{
  background: url(../img/team__footer.svg) no-repeat center bottom -400px / 2668px auto;
}

.score__table{
  margin-bottom: 90px;
}

.score{
  padding-bottom: 42px;
}

.score__body .score__text{
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
}
.score__head .score__text{
  font-size: 26px;
  background-color: #10ACC5;
}

.score__bottom{
  padding: 16px 24px;
  display: flex;
  justify-content: flex-end;
  background: #10ACC5;
  .btn{
    height: 56px;
    font-size: 14px;
    max-width: 200px;
  }
}

.block-404__title {
  text-align: center;
  font-weight: bold;
  font-size: 288px;
  line-height: 87.07%;
  font-family: $calibri;
  margin-bottom: 0;
  text-align: center;
  color: #fff;
}

.block-404__text{
  text-align: center;
  font-weight: bold;
  font-size: 32px;
  line-height: 105.57%;
  font-family: $calibri;
  margin-bottom: 47px;
  color: #fff;
}
.block-404__btn{
  display: flex;
  justify-content: center;
}
.block-404__btn .btn{
  max-width: 225px;
  font-size: 14px;
  line-height: 16px;
}



.form--404  .form__wrap{
  display: flex;
  flex-wrap: wrap;
}
@media(max-width: 1300px){
  .form--404 {
    background: url(../img/inner-1.svg) no-repeat top left/1440px auto;
    background-color: #7BCFBF;
    .form__wrap {
      min-height: calc(100vh - 165px);
  }
  }
  .next, .prev{
    display: none;
  }
  .score__number ,
  .score__point{
    width: 59px;
  }
  .score__text {
    width: calc(100% - 590px);
  }
  .score__head .score__text{
    font-size: 16px;
    line-height: 19px;
  }
  .score__body .score__text{
    font-size: 14px;
    line-height: 16px;
    padding-right: 5px;
  }
  .score__table{
    min-width: 840px;
  }
  .score__table-scroll{
    overflow-y: auto;
    width: calc(100% + 40px);
    margin-left: -20px;
    padding-left: 20px;
    padding-right: 20px;
  }
}



@media(max-width: 980px){
  .gallery__slider{
    .swiper-slide{
      width: 237px;
    }
  }
  .video__slider{
    .swiper-slide{
      width: 237px;
    }
  }
  .gallery__description{
    font-size: 16px;
  }
  .video__frame iframe{
    height: 127px;
  }
  .svgsprite._play{
    width: 35px;
    height: 35px;
  }
  .video__link{
    font-size: 14px;
  }
  .video{
    height: auto;
    min-height: auto;
  }
  .team__pic{
    width: 100%;
    height: calc(100vh / 2);
    margin-bottom: 28px;
  }
  .team__doc {
    width: calc(100% - 20px);
    padding: 20px;
  }
  .team__doc-ico{
    width: 54px;
    height: 54px;
    .svgsprite._load{
      width: 30px;
    height: 30px;
    }
  }
  .team__info{
    width: 100%;
  }
  .team__head{
    background-position: top left - 500px;
    background-size: auto 100%;
    padding-bottom: 170px;
    margin-bottom: 15px;
    .inner__title{
      margin: 0 auto 28px;
    }
  }
  .team__project-left{
    width: 100%;
    margin-bottom: 37px;
  }
  .team__project-right{
    width: 100%;
  }
  .team__doc-text{
    font-size: 16px;
    line-height: 19px;
    width: calc(100% - 74px);
  }
}

@media(max-width: 768px){
  .block-404__title{
    font-size: 160px;
    line-height: 87.07%;
  }
  .block-404__text {
    font-size: 20px;
    line-height: 23px;
    margin-bottom: 30px;
  }
  .team__project-title{
    font-size: 26px;
    line-height: 105.57%;
    margin-bottom: 28px;
  }
  .block-404__btn .btn{
    max-width: 300px;
    width: 100%;
  }
  .team__tag {
    font-size: 16px;
    white-space: nowrap;
    margin-bottom: 10px;
  }
  .team__tags{
    flex-wrap: wrap;
  }
  .team__tag.--white-arrow{
    width: 73px;
  }
  .team__description{
    font-size: 18px;
    line-height: 21px;
    margin-bottom: 34px;
  }
  .team__region{
    font-size: 18px;
    line-height: 21px;
  }
  .team__head{
    padding-bottom: 110px;
  }
}