.header{
  background-color: #7BCFBF;
  padding: 20px 0;
}

.header__logo{
  margin-right: 65px;
}

.nav__link{
  font-size: 18px;
  text-transform: uppercase;
  margin-right: 46px;
  color: #fff;
  position: relative;
  &:after{
    content: "";
    position: absolute;
    top: 100%;
    margin-top: 18px;
    width: 100%;
    height: 4px;
    background-color: #fff;
    left: 0;
    opacity: 0;
    transition: .4s;
  }
  &:hover{
    &:after{
      opacity: 1;
    }
  }
  &.active{
    &:after{
      opacity: 1;
    }
  }
  &:last-child{
    margin-right: 0px;
  }
}


.mobile__header {
  display: none;
  padding-top: 26px;
  padding-bottom: 26px;
  background-color: #7BCFBF;
}

.burger{
  width: 56px;
  height: 56px;
  border: 1px solid #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
}

.mobile-enter{
  width: 56px;
  height: 56px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  background-color: #DDFF54;
}

.mobile-menu{
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 2000;
  background: url(../img/mob_menu.svg) no-repeat top right / cover;
  background-color: #7BCFBF;
}

.mobile-menu__header{
  padding-top: 26px;
  padding-bottom: 26px;
  background-color: #7BCFBF;
  margin-bottom: 53px;
  
}

.mobile-menu {
  overflow-y: auto;
  display: none;
  &.active{
    display: block;
  }
  .nav__link{
    display: block;
    width: 100%;
    margin-bottom: 37px;
    text-align: center;
    a{
      color: #fff;
      background-color: #7BCFBF;
      display: inline-block;
      padding: 3px;
    }
    &.active{
      a{
        color: rgba(#fff, .5);
      }
      &:after{
        display: none;
      }
    }
  }
  .btn{
    margin-top: 60px;
  }
}

@media(max-width: 980px){
  .header{
    display: none;
  }
  .mobile__header{
    display: block;
  }
}